@import url("https://fonts.googleapis.com/css2?family=PT+Sans:ital,wght@0,400;0,700;1,400;1,700&display=swap");

body {
  margin: 0;
  font-family: "PT Sans", sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.headerAuth {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-left: 10px;
  margin-right: 45px;
}

.headerReg {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin-left: 45px;
}

.headerReg p {
  padding-top: 10px;
  padding-right: 15px;
  color: #4a9eff;
}

h1 {
  font-size: 26px;
  text-align: center;
  font-weight: 400;
}
p {
  font-size: 16px;
}
.row {
  display: flex;
  justify-content: space-between;
  border-bottom: solid 1px rgb(225, 225, 225);
  padding-left: 20px;
  padding-right: 20px;
}

.buttons {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  padding-left: 20px;
  max-width: 100%;
  padding-right: 10px;
}

.form {
  display: flex;
  padding: 0;
  flex-direction: row;
  flex-grow: 1;
  width: 100%;
}

.search__form {
  display: flex;
  height: 40px;
  width: 100%;
  margin-right: 10px;
}

.search__form input {
  padding: 10px;
  margin: 0;
  border: solid 2px rgb(199, 199, 199);
  border-radius: 10px;
  width: 100%;
  font-size: 16px;
}

.default__form {
  margin-top: 50px;
  display: flex;
  flex-direction: column;
  height: 40px;
  max-width: 100%;
  font-family: "PT Sans", sans-serif;
}

.default__form label {
  display: flex;
  flex-direction: column;

  font-family: "PT Sans", sans-serif;
}

.input__default {
  padding: 10px;
  border: solid 2px rgb(199, 199, 199);
  border-radius: 10px;
  font-size: 16px;
  margin-bottom: 10px;
  margin-left: 10px;
  margin-right: 10px;
  font-family: "PT Sans", sans-serif;
}

.input__mistake {
  font-family: "PT Sans", sans-serif;
  padding: 10px;
  border: solid 2px rgb(255, 194, 194);
  border-radius: 10px;
  font-size: 16px;
  margin-bottom: 10px;
  margin-left: 10px;
  margin-right: 10px;
  animation-duration: 0.3s;
  animation-name: horizontal-shaking;
  animation-iteration-count: 1;
}

@keyframes horizontal-shaking {
  0% {
    transform: translateX(0);
  }
  25% {
    transform: translateX(2px);
  }
  50% {
    transform: translateX(-2px);
  }
  75% {
    transform: translateX(2px);
  }
  100% {
    transform: translateX(0);
  }
}

.default__form input[type="submit"] {
  font-family: "PT Sans", sans-serif;
  background: #5744ff;
  color: white;
  border: 0 none;
  cursor: pointer;
  -webkit-border-radius: 5px;
  border-radius: 5px;
  height: 40px;
  padding: 10px;
  margin-left: 10px;
  margin-right: 10px;
}

.submitContainer {
  display: flex;
  justify-content: right;
}

.submitImage {
  width: 65px;
  height: 65px;
  margin-top: 100%;
  margin-right: 10px;
}

a {
  color: black;
  text-decoration: none;
}

.button {
  width: 40px;
  height: 40px;
  padding-right: 5px;
}

.mapAll {
  width: 100%;
  height: 100%;
}

.mapContainer {
  display: flex;
  width: 100%;
  height: 100vh;
}

.tabbar {
  position: absolute;
  display: flex;
  justify-content: space-between;
  height: 79px;
  width: 100%;
  margin: 0;
  bottom: 0;
  background-color: white;
  z-index: 500 !important;
  padding-top: 5px;
  box-shadow: 0 -5px 5px -5px rgba(215, 215, 215, 0.5);
  box-sizing: border-box;
  padding-left: 30px;
  padding-right: 30px;
}

.tab {
  justify-content: center;
  display: flex;
  flex-direction: column;
  align-items: center;
  margin: 0;
}

.tabTextDefault {
  margin-bottom: 0;
  color: #a9a9a9;
}

.tabTextActive {
  margin-bottom: 0;
  color: #4a79ff;
}

.mapimg {
  width: 27px;
  height: 39px;
}

.chargeimg {
  width: 32.5px;
  height: 35.5px;
}

.profileimg {
  width: 28px;
  height: 33px;
}

.stationForMapRow {
  display: flex;
  flex-direction: row;
  padding-left: 10px;
  background-color: #fdfdfd;
  margin-left: 20px;
  margin-right: 20px;
  margin-bottom: 10px;
  border-radius: 20px;
}

.stationForMapRow p {
  font-size: 16px;
}

.stationForMapRowInner {
  display: flex;
  width: 75%;
  flex-direction: row;
  justify-content: space-between;
  padding-right: 10px;
}

.stationForMapRowInnerLeft {
  display: flex;
  flex-direction: row;
}

.stationForMapImage {
  height: 40px;
  width: 40px;
  margin-left: 0;
  padding-top: 5px;
  padding-right: 2px;
}

.stationForMapImage2 {
  height: 30px;
  width: 30px;
  padding-top: 10px;
  padding-left: 5px;
  padding-right: 7px;
}

.stationForMapRowStatus {
  display: flex;
  flex-direction: row;
  justify-content: center;
  background-color: #4eec8d;
  width: 25%;
  border-radius: 0 20px 20px 0;
}

.stationName {
  font-family: "PT Sans", sans-serif;
  font-size: 22px;
  font-weight: 700;
  padding-left: 25px;
  margin-bottom: 0;
}

.stationAddress {
  font-family: "PT Sans", sans-serif;
  font-size: 18px;
  font-weight: 400;
  padding-left: 25px;
  margin-top: 10px;
  padding-bottom: 20px;
}

.phone {
  padding-left: 20px;
}

.phone p {
  font-size: 22px;
}

.wallet {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  background-color: #4a79ff;
  border-radius: 20px;
  box-sizing: border-box;
  width: auto;
  height: 200px;
  margin-left: 20px;
  margin-right: 20px;
  padding: 20px;
  color: white;
  margin-bottom: 20px;
}
.wallet p {
  font-size: 22px;
  margin: 0;
}

.profileButton {
  display: flex;
  flex-direction: row;
  justify-content: center;
  font-family: "PT Sans", sans-serif;
  background: #ffffff;
  color: #d9d9d9;
  border: 1px solid #d9d9d9;
  cursor: pointer;
  -webkit-border-radius: 5px;
  border-radius: 10px;
  height: 20px;
  padding: 10px;
  margin-left: 20px;
  margin-right: 20px;
  margin-top: 10px;
}

.profileButton p {
  margin: 0;
}
